@import "default-variables";

/**
 * Place your custom styles here.
 */

.path-user {
  .main-container {

    .main-header {
      width: 100vw;
      background-color: black;
      background-image: url('../images/banner/login.jpg');
      background-size: cover;
      height: 300px;
      background-position-x: center;
      //  padding-left: 25%;
      //  padding-top: 5%;

      h1.page-header {
        background-color: white;
        display: inline-block;
        margin-left: 25%;
        margin-top: 5%;
        padding: 10px;
        position: relative;
        border: none;
        //width: 25ch;
        //max-width: 30vw;
        //white-space: nowrap;

        @media (max-width: 1199px) {
          margin-left: 5%;
        }

        &:after {
          content: "";
          position: absolute;
          background-image: url(../images/white_badge_edge.png);
          background-repeat: no-repeat;
          background-size: contain;
          top: 0;
          bottom: 0;
          width: 200%;
          right: -199%;
        }
      }
    }
    .region.region-content {
      //display: flex;
      //justify-content: center;
      //padding: 20px;

      form {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: white;
        background-color: $deep-blue;
        max-width: 320px;
        margin: 100px auto;
        padding: 20px;

        &:before {
          content: "";
          position: absolute;
          background-color: #f5f5f5;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          z-index: -1;
        }

        a {
          color: #94c2ef;
        }

        button.form-submit {
          background-color: #94c2ef;
          color: #2b3552;
        }

        div#edit-timezone--content {
          color: $deep-blue;
        }
      }
    }
  }
}
